import React from 'react';
import axios from 'axios';
import Movie from './Movie';
import "./App.css"


class App extends React.Component {
  state = {
    isFetching: true,
    movies: [],
  };

  getMovie = async () => {
    const {
      data: {
        data: {movies},
      },
    } = await axios.get('https://yts.mx/api/v2/list_movies.json');
    this.setState({movies, isFetching: false});
  };

  componentDidMount() {
    this.getMovie();
  }
  
  render() {
    const {isFetching, movies} = this.state;
    return (
      <section className="container">
        {isFetching ? (
          <div className="loader">Loadingâ€¦</div>
        ) : (
          movies.map((movie) => {
            return (
              <div className="movies">
                <Movie
                  key={movie.id}
                  title={movie.title}
                  summary={movie.summary}
                  year={movie.year}
                  rating={movie.rating}
                  poster={movie.medium_cover_image}
                  genres={movie.genres}
                />
              </div>
            );
          })
        )}
      </section>
    );
  }
}

export default App;